      title: 'Libreria Prompt Pronti all\'Uso',
      description: 'Collezione di prompt testati e ottimizzati per le situazioni più comuni dell\'amministratore.',
      content: `# Libreria Prompt Pronti all'Uso

## Comunicazioni Condominiali

### Sollecito Pagamento Diplomatico
\`\`\`
RUOLO: Sei un amministratore condominiale esperto in comunicazione diplomatica
AZIONE: Redigi un sollecito di pagamento formale ma cortese
CONTESTO: [Condomino nome], [importo], [scadenza], [tipo spesa], [eventuali precedenti]
ESEMPIO: Tono professionale ma comprensivo, formato email, riferimenti normativi, modalità pagamento, conseguenze progressive
\`\`\`

### Comunicazione Lavori Straordinari
\`\`\`
RUOLO: Sei un amministratore esperto in gestione lavori condominiali
AZIONE: Redigi comunicazione per lavori straordinari con gestione disagi
CONTESTO: [Tipo lavori], [durata], [orari], [aree interessate], [misure sicurezza]
ESEMPIO: Informativo ma rassicurante, cronoprogramma chiaro, contatti emergenza, benefici finali, 200-300 parole
\`\`\`

### Risposta a Reclamo Complesso
\`\`\`
RUOLO: Sei un mediatore professionale specializzato in conflitti condominiali
AZIONE: Rispondi a reclamo gestendo aspetti emotivi e fornendo soluzioni
CONTESTO: [Natura reclamo], [stakeholder coinvolti], [precedenti], [vincoli normativi]
ESEMPIO: Empático ma autorevole, riconoscimento problema, proposta soluzione concreta, passi successivi
\`\`\`

## Analisi Documenti

### Riassunto Verbale Assemblea
\`\`\`
RUOLO: Sei un segretario assembleare esperto in sintesi documentale
AZIONE: Riassumi questo verbale evidenziando decisioni e scadenze
CONTESTO: [Testo verbale completo]
ESEMPIO: Bullet points per argomento, decisioni prese con maggioranze, azioni assegnate con scadenze, 150-200 parole
\`\`\`

### Analisi Contratto/Preventivo
\`\`\`
RUOLO: Sei un consulente legale specializzato in contratti condominiali
AZIONE: Analizza questo contratto identificando punti critici e opportunità
CONTESTO: [Testo contratto/preventivo]
ESEMPIO: Tabella con: clausole importanti, rischi identificati, vantaggi, raccomandazioni, domande da porre al fornitore
\`\`\`

### Comparazione Preventivi
\`\`\`
RUOLO: Sei un analista esperto in valutazione fornitori
AZIONE: Confronta questi preventivi per [servizio] evidenziando differenze chiave
CONTESTO: [Preventivo 1], [Preventivo 2], [Preventivo 3], [criteri prioritari]
ESEMPIO: Tabella comparativa: costi, servizi inclusi/esclusi, vantaggi/svantaggi, raccomandazione motivata
\`\`\`

## Gestione Assemblee

### Preparazione Ordine del Giorno
\`\`\`
RUOLO: Sei un amministratore esperto in gestione assemblee
AZIONE: Struttura ordine del giorno ottimale per assemblea [ordinaria/straordinaria]
CONTESTO: [Argomenti da trattare], [bilancio/lavori/questioni], [tempo disponibile], [criticità previste]
ESEMPIO: Sequenza logica, tempi stimati, documentazione allegata, strategie per punti controversi
\`\`\`

### Gestione Conflitti in Assemblea
\`\`\`
RUOLO: Sei un mediatore esperto in dinamiche assembleari
AZIONE: Suggerisci strategie per gestire conflitto su [argomento specifico]
CONTESTO: [Natura conflitto], [posizioni in campo], [interessi sottostanti], [vincoli normativi]
ESEMPIO: Tecniche de-escalation, opzioni compromesso, framing positivo, gestione tempo, alternative creative
\`\`\`

## Questioni Legali e Normative

### Interpretazione Normativa
\`\`\`
RUOLO: Sei un esperto in normativa condominiale aggiornata
AZIONE: Spiega implicazioni pratiche di [normativa/sentenza] per amministrazione condominiale
CONTESTO: [Testo normativo], [situazione specifica del condominio]
ESEMPIO: Linguaggio accessibile, implicazioni operative concrete, azioni richieste, scadenze, rischi non conformità
\`\`\`

### Gestione Morosità
\`\`\`
RUOLO: Sei un esperto in recupero crediti condominiali
AZIONE: Sviluppa strategia recupero per condomino moroso
CONTESTO: [Importo], [durata morosità], [situazione finanziaria nota], [rapporti precedenti]
ESEMPIO: Piano graduale: comunicazioni, tempistiche, procedure legali, alternative, documentazione necessaria
\`\`\`

## Innovazione e Sostenibilità

### Proposta Efficientamento Energetico
\`\`\`
RUOLO: Sei un consulente esperto in efficienza energetica condominiale
AZIONE: Sviluppa proposta efficientamento per presentazione assemblea
CONTESTO: [Tipo edificio], [anno costruzione], [consumi attuali], [budget disponibile]
ESEMPIO: Analisi costi-benefici, incentivi disponibili, ROI stimato, impatto ambientale, fasi implementazione
\`\`\`

### Valutazione Tecnologie Smart
\`\`\`
RUOLO: Sei un esperto in tecnologie smart building per condomini
AZIONE: Valuta opportunità implementazione [tecnologia specifica]
CONTESTO: [Caratteristiche condominio], [esigenze abitanti], [budget], [competenze tecniche disponibili]
ESEMPIO: Pro/contro, costi implementazione/gestione, benefici concreti, tempistiche, fornitori raccomandati
\`\`\`

## Tips per Ottimizzare i Prompt

### 1. Personalizzazione del Contesto
- Aggiungi sempre dettagli specifici del tuo condominio
- Menziona caratteristiche uniche (età edificio, composizione condomini)
- Includi vincoli normativi locali se rilevanti

### 2. Iterazione e Raffinamento
- Usa il primo output come base
- Chiedi modifiche specifiche: "Rendi il tono più formale"
- Richiedi varianti: "Dammi 3 versioni diverse"

### 3. Controllo Qualità
- Sempre prompt di verifica: "Controlla questo testo per errori"
- Chiedi check conformità: "Verifica aderenza a normativa XYZ"
- Valida logica: "Questo ragionamento è corretto?"

### 4. Adattamento al Pubblico
- Specifica sempre il destinatario
- Adatta linguaggio e registro
- Considera livello di conoscenza tecnica

Ricorda: questi prompt sono template da personalizzare. Adattali sempre alla tua situazione specifica!`
