// Funzione per generare e scaricare file TXT
const downloadTxt = (content, filename) => {
  const element = document.createElement('a');
  const file = new Blob([content], { type: 'text/plain' });
  element.href = URL.createObjectURL(file);
  element.download = filename;
  document.body.appendChild(element);
  element.click();
  document.body.removeChild(element);
};

function ResourceArea() {
  return (
    <div className="resource-area">
      {/* Disclaimer */}
      <div className="resource-disclaimer">
        <strong>Disclaimer:</strong> I contenuti di quest'area sono forniti a scopo informativo e formativo. 
        Non costituiscono consulenza legale, tecnica o professionale specifica. È sempre necessario verificare 
        le informazioni con fonti ufficiali e consultare professionisti qualificati per situazioni specifiche. 
        L'uso di questi materiali è sotto la responsabilità dell'utilizzatore.
      </div>

      <div className="resource-categories">
        {/* Framework */}
        <div className="resource-category">
          <h3>🧭 Framework e Metodologie</h3>
          <div className="resource-grid">
            {resourcesData.framework.map(resource => (
              <div key={resource.id} className="resource-card">
                <h4>{resource.title}</h4>
                <p>{resource.description}</p>
                <button 
                  className="download-button"
                  onClick={() => downloadTxt(resource.content, `${resource.id}.txt`)}
                >
                  📄 Scarica TXT
                </button>
              </div>
            ))}
          </div>
        </div>

        {/* Guide Normative */}
        <div className="resource-category">
          <h3>⚖️ Guide Normative</h3>
          <div className="resource-grid">
            {resourcesData.guide.map(resource => (
              <div key={resource.id} className="resource-card">
                <h4>{resource.title}</h4>
                <p>{resource.description}</p>
                <button 
                  className="download-button"
                  onClick={() => downloadTxt(resource.content, `${resource.id}.txt`)}
                >
                  📄 Scarica TXT
                </button>
              </div>
            ))}
          </div>
        </div>

        {/* Strumenti */}
        <div className="resource-category">
          <h3>🔧 Strumenti Pratici</h3>
          <div className="resource-grid">
            {resourcesData.strumenti.map(resource => (
              <div key={resource.id} className="resource-card">
                <h4>{resource.title}</h4>
                <p>{resource.description}</p>
                <button 
                  className="download-button"
                  onClick={() => downloadTxt(resource.content, `${resource.id}.txt`)}
                >
                  📄 Scarica TXT
                </button>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default ResourceArea;